.header{padding:0 2.5rem;height:var(--headerHeight);display:flex;align-items:center;margin:0;width:100%;justify-content:space-between;background-color:var(--white)}.wrapper>h2{color:var(--title);font-size:clamp(1.25rem,1.073943662rem + 0.7511737089vw,1.75rem);font-weight:600;margin:0}.wrapper>h2>span{display:block}.controles,.search{display:flex}.controles{align-items:center;gap:max(1.25rem,2vw)}.item,.search{background-color:var(--background);max-height:3.125rem;height:max(2.5rem,3.125vw)}.item{max-width:3.125rem;min-width:2.5rem;width:max(2.5rem,3.125vw);border:none;border-radius:100px}.item>img{margin:0 auto}.search{border-radius:100px;padding:0 clamp(1.25rem,1.5vw,1.5625rem);gap:0.9375rem}.search>input{color:var(--text-color);background-color:rgba(0,0,0,0);overflow:hidden;outline:none;border:none}.search>input::placeholder{color:var(--text-color);font-size:0.875rem}.text{display:contents}.wrapper{display:contents}.searchWrapper{display:none}.burger{display:none;width:2.5rem;height:2.5rem;border:none;background-color:rgba(0,0,0,0)}.burger>span{margin:5 auto;display:block;margin-bottom:4px;width:18px;border-radius:15px;height:2px;background-color:var(--title)}.avatar>img{width:60px;height:60px;object-fit:cover;border-radius:100px}.isActive{position:relative;z-index:15}.isActive :nth-child(2){display:none}.isActive :first-child{rotate:-45deg;margin-bottom:-5px}.isActive :last-child{rotate:45deg;margin-top:3px}@media(width <= 1024px){.header{height:150px;flex-direction:column;width:100%;gap:0.625rem;padding:1.25rem 2.5rem}.wrapper{width:100%;display:flex;justify-content:space-between;align-items:center}.text{display:none}.searchWrapper{width:100%;display:block}.searchWrapper>.search{width:70%;margin:0 auto}.burger{display:block}}